/* horizontal padding */
.hp0 {
  padding-left: 0px;
  padding-right: 0px;
}

.hp1 {
  padding-left: 1%;
  padding-right: 1%;
}

/* vertical padding */
.vp0 {
  padding-left: 0px;
  padding-right: 0px;
}

.vp1 {
  padding-left: 1%;
  padding-right: 1%;
}

/* horizontal margin */
.hm0 {
  margin-left: 0px;
  margin-right: 0px;
}

.hm1 {
  margin-left: 1%;
  margin-right: 1%;
}

/* vertical margin */
.vm0 {
  margin-top: 0px;
  margin-bottom: 0px;
}

.vm1 {
  margin-top: 1%;
  margin-bottom: 1%;
}

.bold {
  font-style: bold;
}

.fw900 {
  font-weight: 900;
}

.fw700 {
  font-weight: 700;
}

.row {
  display: flex;
}

.col {
  display: flex;
  flex-direction: column;
}

.wrap {
  flex-wrap: wrap;
}

.flexAIC {
  align-items: center;
}

.flexJCC {
  justify-content: center;
}

.flexSB {
  justify-content: space-between;
}

.flexSA {
  justify-content: space-around;
}

.scrolly {
  overflow-y: scroll;
}

.scrollx {
  overflow-x: scroll;
}

.circle {
  border-radius: 50%;
}

.line {
  border: 1px solid rgb(202, 202, 202);
}

.linebottom {
  border-bottom: 1px solid rgb(202, 202, 202);
}

.linetop {
  border-top: 1px solid rgb(202, 202, 202);
}

.lineleft {
  border-left: 1px solid rgb(202, 202, 202);
}

.lineright {
  border-right: 1px solid rgb(202, 202, 202);
}

.brd {
  border-radius: 5px;
}

* {
  font-size: 16px;
}

button:hover {
  color: rgb(8, 73, 33);
}

/* specific */

th {
  font-size: 18px;
  text-align: left;
  background-color: #6b5b95;
  color: white;
  padding: 1%;
}

th.desc {
  background-color: rgb(207, 0, 97);
}

td,
th {
  padding: 10px;
}

table,
th,
td {
  border: 1px solid rgb(17, 130, 60);
  border-collapse: collapse;
}

.btn {
  box-shadow: rgba(0, 0, 0, 0.17) 0px -23px 25px 0px inset, rgba(0, 0, 0, 0.15) 0px -36px 30px 0px inset, rgba(0, 0, 0, 0.1) 0px -79px 40px 0px inset, rgba(0, 0, 0, 0.06) 0px 2px 1px, rgba(0, 0, 0, 0.09) 0px 4px 2px, rgba(0, 0, 0, 0.09) 0px 8px 4px, rgba(0, 0, 0, 0.09) 0px 16px 8px, rgba(0, 0, 0, 0.09) 0px 32px 16px;
}

.btn:hover {
  /* background-color: rgb(255, 251, 0) !important; */
  /* color: black !important; */
  box-shadow: none;
}

a.active {
  background-color: #abd8c5 !important;
}

td {
  font-size: 1rem;
  color: gray;
}

td span {
  font-weight: 700;
  color: rgb(37, 24, 2);
}

pre {
  font-size: 11px !important;
}

#data {
  width: 100%;
  max-height: 93vh;
  overflow-x: scroll;
  overflow-y: scroll;
}


/* Extra small devices (phones, 600px and down) */
@media only screen and (max-width: 600px) {
.topdiv{
  flex-direction: column;
  align-items: center;
}
.col{
  width: 90%;
  padding: 5%;
}
}

